.buttonBox {
    width: 100%;
    height: calc(100% - 110px);
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: repeat(5, 1fr);
    grid-gap: 10px;
    background-color: #70a3c4;
  background-image: linear-gradient(315deg, #34fbf16a 0%, #00ff8480 74%);
  }