.screen {
    height: 100px;
    width: 100%;
    margin-bottom: 10px;
    padding: 0 10px;
    background-color: #5d927b2d;
    border-radius: 10px;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    color: rgba(255, 255, 255, 0.521);
    font-weight: bold;
    box-sizing: border-box;
  }